body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f9;
  color: #333;
}

.resume-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

header {
  background-color: #2c3e50;
  color: white;
  padding: 40px 0;
  text-align: center;
}

h1 {
  font-size: 2.5rem;
  margin: 0;
}

nav {
  background-color: #34495e;
  padding: 10px;
}

nav ul {
  list-style-type: none;
  padding: 0;
  text-align: center;
}

nav ul li {
  display: inline;
  margin: 0 15px;
}

nav ul li a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
}

section {
  margin: 50px 0;
}

h2 {
  color: #16a085;
}

.skills-container .skill {
  margin-bottom: 20px;
}

.progress-bar {
  width: 100%;
  background-color: #ddd;
  border-radius: 5px;
  height: 20px;
}

.progress-bar-inner {
  height: 100%;
  background-color: #16a085;
  border-radius: 5px;
}

/* Add smooth scrolling effect */
html {
  scroll-behavior: smooth;
}

@media (max-width: 768px) {
  nav ul li {
    display: block;
    margin-bottom: 10px;
  }
}
